<template>
  <div id="app">
    <!-- src="http://localhost:8081/network-detection.html" -->
    <!-- src="https://sit-classroom.uuabc.com/network/" -->
    <iframe
      v-if="version"
      id="network-detection"
      src="http://localhost:8081/network-detection.html?lang=en_US"
      frameborder="0"
      allow="autoplay; camera; microphone"
    ></iframe>
  </div>
</template>

<script>
import { setTimeout } from 'timers';

export default {
  name: 'App',
  data() {
    return {
      version: true
    }
  },
  mounted() {
    const that = this;
    window.onload = () => {
      const networkDetection = {
        origin: 'http://localhost:8081'
      }
      // const childUrl = 'http://localhost:8081/network-detection.html';
      // const childUrl = 'https://sit-classroom.uuabc.com/network/';
      const childUrl = 'http://localhost:8081/network-detection.html';

      function postMessage(data) {
        document.getElementById('network-detection').contentWindow.postMessage(data, childUrl);
      }

      function receiveMessage(event) {
        // 我们能相信信息的发送者吗?  (也许这个发送者和我们最初打开的不是同一个页面).
        if (event.origin !== networkDetection.origin)
          return;
        //以下是业务逻辑
        console.log(event.data);
        const {
          type,
          data
        } = event.data;
        if (!type) return;
        switch (type) {
          case 'report':
            // save to database
            //then tell the iframe you had saved
            document.getElementById('network-detection').contentWindow.postMessage({
              type: 'config',
              data: {
                tips: "出现异常，无法正常上课，<br>请调试异常项后，重新检测。",
                goBackText: "会员中心",
                goAheadText: "测网教室"
              }
            },childUrl);
            // setTimeout(() => {
            //   postMessage({
            //     type: 'config',
            //     data: {
            //       tips: "出现异常，无法正常上课，<br>请调试异常项后，重新检测。",
            //       goBackText: "会员中心",
            //       goAheadText: "测网教室"
            //     }
            //   });
            // }, 5000)
            break;
          case 'goBack':
            that.version = false;
            that.$nextTick(() => {
              that.version = true;
            })
            // that.version = true;
            break;
          case 'goAhead':
            location.href = 'http://taobao.com';
            break;
        }
      }

      window.addEventListener("message", receiveMessage, false);

    }
  }
}
</script>

<style lang="less">
#app {
  iframe {
    width: 100%;
    height: 100vh;
  }
}
</style>
